body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  display: flex;
  justify-content: center;
}

form {
  display: flex;
  flex-direction: column;
  width: 480px;
}

input {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-top: 10px;
}

.error {
  color: red;
}

button {
  padding: 10px;
  background: blue;
  border: 1px solid blue;
  border-radius: 5px;
  margin-top: 10px;
  color: #fff;
  cursor: pointer;
}

.flex {
  display: flex;
}

.flex > div {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}

select {
  background: transparent;
  padding: 5px;
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.dropdown {
  display: none;
  color: #808080;
  font-size: 14px;
}

.dropdown.show {
  display: flex;
  flex-direction: column;
  max-height: 111px;
  overflow-y: auto;
}

.dropdown-item {
  padding: 10px;
  border-width: 1px;
  border-style: solid;
  border-color: #ccc;
  border-top: none;
}
